.text {
    font-size: 18px;
    font-weight: 400;
    line-height: 1.44;
    display: flex;
    flex-direction: column;
    row-gap: 20px;
}

@media (max-width: 840px) {
    .text {
        font-size: 16px;
    }
}
@media (max-width: 540px) {
    .text {
        font-size: 15px;
    }
}
@media (max-width: 949px) {
    .text {
        flex: 0 1 100%;
    }
}
.text__list {
    display: flex;
    flex-direction: column;
    row-gap: 8px;
}
.text__list li span{
    font-weight: 600;
    color: #6b6a6a;
}

.text__education span {
    display: block;
}
